.advertise {
  background: linear-gradient(112.5deg, rgba(87, 142, 251, 0.36) 0.41%, rgba(255, 0, 204, 0.36) 14.08%, rgba(74, 157, 255, 0.45) 59.96%, rgba(23, 111, 191, 0.88) 98.62%);
  
}
.advertise > div{
  display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100% - 80px);
}
.photo-and-name{
  display: flex;
}
.profile-block button:hover{
  cursor: pointer;
}
.profile-block button{
  font-family: 'Inter';
  cursor: pointer;
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
padding: 5px 0;
color: rgba(0, 0, 0, 0.78);
background: #FFFFFF;
border: 1px solid #000000;
border-radius: 35px;
width: 250px;
}
.item-reward{
  display: flex;
}
.item-reward img{
  margin-left: auto;
    margin-bottom: -6px;
}
.profile-block .phone-button{
  margin-bottom: 20px;
}
.item-description{
  background: rgba(255, 255, 255, 0.8);
border-radius: 23px;
text-align: left;
padding: 20px;
box-sizing: border-box;
}
.views-count{
  font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 15px;
line-height: 18px;
margin-top: 4px;
color: rgba(0, 0, 0, 0.78);
}
.map-location{
  background: #FFFFFF;
border-radius: 24px;
padding: 25px;
}
.map-location iframe{
  border: none;
}
.login-point{
  display: flex;
  justify-content: space-between;
}
.login-point > img{
  max-width: 40px;
  max-height: 40px;
  margin: auto 0;
}
.login-point-text > img{
  max-width: fit-content;
}
.login-point-text{
  display: flex;
  margin-left: 10px;
}
.login-point-text p{
  font-family: 'Inter';
  text-align: left;
  margin-left: 5px;
font-style: normal;
font-weight: 500;
font-size: 12px;
line-height: 15px;

color: #000000;
}
.advertise .form-row {
  width: 90%;
  margin: 0 auto;
}
.advertise .form-row:nth-child(2){
  margin-top: 20px;
}
.map-location p{
  font-family: 'Inter';
font-style: normal;
margin-top: 0;
font-weight: 500;
font-size: 18px;
line-height: 24px;

color: rgba(0, 0, 0, 0.78);
}
.item-description p{
  font-family: 'Inter';
font-style: normal;
font-weight: 700;
font-size: 18px;
padding-bottom: 10px;
border-bottom: 0.5px solid #000000;
line-height: 24px;
text-align: justify;
margin: 0;
color: rgba(0, 0, 0, 0.78);

}
.profile-block p{
  font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
text-align: left;
color: rgba(0, 0, 0, 0.78);
}
.profile-block{
  background: #FFFFFF;
border-radius: 22px;
padding: 20px;
}
.user-name{
  font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 20px;
line-height: 24px;
margin: auto 0;
margin-left: 20px;
color: rgba(0, 0, 0, 0.78);
}
.item-name,.item-location,.item-reward{
  
font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 18px;
line-height: 22px;
width: 270px;
  max-width: 270px;
  height: 35px;
  box-sizing: border-box;
  margin-bottom: 25px;
background: #FFFFFF;
border-radius: 35px;
padding: 6px 14px;
text-align: left;
color: rgba(0, 0, 0, 0.78);


}