
.container-3 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(107.56deg, rgba(255, 0, 204, 0.37) 0%, rgba(151, 87, 224, 0.57) 52.6%, rgba(0, 87, 255, 0.66) 100%);

}
.grid-container {
  opacity: 0;
  display: none;
  transition: opacity 0.5s ease-in-out;
}

.grid-container.active {
  opacity: 1;
  display: block;
}


.grid-row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}

.grid-col {
  flex: 1;
  padding: 0 15px;
  margin-bottom: 20px;
}
.page-pagination{
  position: relative;
}
.grid-col img {
  width: 100%;
  height: auto;
  border-radius: 35px;
  width: 150px;
  height: 150px;
  object-fit: cover;
  object-position: center;
}

.pagination-button {
  width: 35px;
  height: 35px;
  background: transparent;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-family: 'Inter';
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  border-radius: 50%;
  line-height: 22px;
  text-transform: uppercase;
  color: #000000;
  border: 5px solid #fff;
  margin-right: 20px;
  cursor: pointer;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.user-menu {
  position: absolute;
  top: 47px;
    right: -16px;
  width: 200px;
  background-color: #fff;
  padding: 10px;
  border: 1px solid;
  border-radius: 20px;
  border-image-source: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.5) 100%);


}

.user-menu ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.user-menu ul li {
  margin-bottom: 10px;
}

.user-menu ul li a {
  text-decoration: none;
  color: #000;
}

.user-menu ul li a:hover {
  text-decoration: underline;
}
